@import "reset.css";

.home_content {
  padding: auto 120px;
  height: 800px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: white;
}

.intro {
  /* display: flex;
  align-items: center;
  justify-content: space-between; */
  height: 700px;
  width: 1200px;
  position: relative;
}

.intro_info {
  position: absolute;
  top: 20%;
  font-size: 50px;
  font-weight: 700;
}
.intro_background {
  width: 1400px;
  height: 700px;
}
