  padding: 0 30px;
} */

.main_info {
  margin-bottom: 20px;
}
.sub_info {
  font-size: 30px;
}

.goToProject {
  font-size: 30px;
  font-weight: 600;
  background-color: #b2bfff;
  color: white;
  padding: 10px 20px;
  border-radius: 20px;
  text-decoration: none;
}
